.header {
  padding: 6rem 0 5rem 0;
}

.navigation {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  padding: 1rem 0;
  height: 8rem;
  width: 100%;
  background-color: $color-secondry;
  box-shadow: 0rem 0.1rem 3rem rgba($color-secondry-dark, 0.4);
  z-index: 2000;
  transition: all 0.3s ease;

  &__search {
    flex-basis: 49%;
    display: flex;
    justify-content: center;
    align-items: center;

    @include addMediaQuery(tablet) {
      justify-content: flex-start;
      flex-basis: 75%;
    }
  }

  &__icon {
    display: flex;
    flex-basis: 51%;

    @include addMediaQuery(tablet) {
      justify-content: flex-end;
      margin: 0 3rem;
      flex-basis: 25%;
    }
  }

  &__logo {
    fill: $color-white;
  }
}

.drawer {
  position: fixed;
  top: 0;
  left: 0;
  display: block;
  width: 12rem;
  transform: translateX(-20rem);
  height: 100vh;
  background-color: rgba($color-secondry, 1);
  transition: all 0.5s;
  z-index: 1700;
  box-shadow: 0rem 1rem 1rem rgba($color-secondry-dark, 0.9);

  &__content {
    text-align: left;
  }

  &__icon-box {
    display: block;
    font-size: 2.3rem;
    margin-left: 4rem;
    position: relative;
    text-align: center;
    cursor: pointer;

    &:hover .drawer__icon-background::after {
      transform: scale(1.4);
      opacity: 1;
    }
  }

  &__icon-background {
    border-radius: 1px;
    height: 2.9rem;
    width: 2.9rem;
    position: absolute;
    top: 3rem;
    left: 0;
    transform: translate(-50%, -50%);
    transition: all 0.5s;
    animation-fill-mode: backwards;
    padding: 0.1rem;

    &::after {
      content: "";
      display: block;
      position: absolute;
      background-color: rgba($color-secondry-dark, 0.2);
      opacity: 0;
      transform: scale(0.3);
      height: 2.9rem;
      width: 2.9rem;
      z-index: 1;
      transition: opacity 0.1s ease, transform 0.3s ease;
    }
  }

  &__icon {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    z-index: 2;
  }

  &__link-icon {
    display: block;
    font-size: 2.4rem;
  }

  &__logo-box {
    text-align: center;
    margin-top: 2rem;
  }

  &__logo {
    transform: translateY(-8.1rem);
  }
}

.drawer-nav {
  margin-top: 5rem;

  &__list {
    font-size: 1.6rem;
    letter-spacing: 0.1;
    font-weight: $font-weight-regular;
    font-family: inherit;
  }

  &__link {
    display: inline-block;
    text-align: center;
    padding: 1.5rem 2rem;
    text-decoration: none;
    font-family: inherit;
    font-weight: 600;
    width: 100%;
    color: $color-white;
    background-color: rgba($color-secondry, 1);
    transition: all 0.5s;

    &:hover {
      background-color: rgba($color-white, 0.59);
      color: $color-secondry;
    }

    &:active {
      background-color: rgba($color-white, 0.59);
      color: $color-secondry;
    }
  }

  &__link--marked {
    color: $color-primary;
  }
}
